import v2 from "./404v2.svg"
import v1 from "./404v1.svg"

const List = {
  v1: v1,
  v2: v2,
}
export default function PageNotFound(props) {
  return (
    <div style={{ textAlign: "center" }}>
      <img src={List.v2} alt="404" style={{ maxHeight: "500px" }} />
      <div className="buttons">
        <button
          className="btn btn-secondary me-2"
          onClick={() => {
            window.history.back()
          }}
        >
          Back
        </button>
        <button
          className="btn btn-primary"
          onClick={() => {
            window.open("/", "_self")
          }}
        >
          Home
        </button>
      </div>
    </div>
  )
}
