import { HCF, HUtils } from "@macashipo/mlib"
import React from "react"
import { Card, CardBody } from "reactstrap"
import { FlowForceUpdateRoutes } from "src/helpers/Flows/Routes"
import PageNotFoundv2 from "src/pages/PageNotFound/404v2"
export default function PageNotFound() {
  let _needUpdateRoutesIn404 = false
  React.useEffect(() => {
    console.warn("render PageNotFound")
    HCF.setCF("just404", {
      href: window.location.href,
      pathname: window.location.pathname,
    })
    _needUpdateRoutesIn404 = HCF.getCF("needUpdateRoutesIn404")
    if (_needUpdateRoutesIn404 === true) {
      FlowForceUpdateRoutes()
      HCF.setCF("needUpdateRoutesIn404", false)
    }
  }, [])
  if (_needUpdateRoutesIn404 === true) {
    return (
      <Card className="ani-fadeIn2s">
        <CardBody className="pt-4"></CardBody>
      </Card>
    )
  }
  return (
    <Card className="ani-fadeIn2s">
      <CardBody className="pt-4">
        <PageNotFoundv2 />
      </CardBody>
    </Card>
  )
}
